// app/layout.tsx
import './globals.css'
import { SelectionProvider } from '../lib/selectionService' // adjust import path if needed

export const metadata = {
  title: 'Editable HTML Poster',
  description: 'Frontend Engineer Evaluation Task',
}

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="en">
      <body>
        {/* Wrap entire app in the SelectionProvider */}
        <SelectionProvider>
          {children}
        </SelectionProvider>
      </body>
    </html>
  )
}

